
body {
    background: #000000;
    height: 100vh;
    margin: 0;
    padding: 0;
    width: 100%;
    overflow-x: hidden;
  }
  
  ::-webkit-scrollbar {
    border-radius: 0.78125vw; 
    border: none;
    width: 0.625vw; 
    background-color: #0b0215;
  }
  
  ::-webkit-scrollbar-thumb {
    border-radius: 0.78125vw;
    animation: HrHomeCar 4s infinite ease-in-out running;
    background-size: 400% 400%;
    background-image: linear-gradient(to top left, #fff75c, #3f00b9);
    transition: all 0.5s ease-in-out;
  }
  
.CyberTruckImg {
    height: 100vh;
    width: 100%;
    background-size: cover;
    background-position: center;
}

  .HomeCarBgCon {
    width: 100%;
    height: 100vh;
    position: relative;
  }
  
  .BgHome {
    background-position: center;
    z-index: -2;
    position: absolute;
    background-size: cover;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .HomeCarHr {
    width: 90%;
    margin: 2.6042vw auto 0; 
    height: 0.3125vw; 
    border-radius: 0.78125vw; 
    background-color: white;
    background-image: linear-gradient(45deg, #ffec11, #a40000);
    background-size: 400% 400%;
    transition: all 0.5s ease-in-out;
    animation: HrHomeCar 4s infinite ease-in-out running;
    border: none;
  }
  
  @keyframes HrHomeCar {
    0% {
      background-position: 0 25%;
    }
    25% {
      background-position: 55% 75%;
    }
    50% {
      background-position: 75% 100%;
    }
    75% {
      background-position: 25% 50%;
    }
    100% {
      background-position: 0 25%;
    }
  }
  
  .HomeCarTxtRow {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0.5208vw; 
    box-sizing: border-box;
  }
  
  .HomeCarTxtClmn {
    width: 100%;
    border-left: 0.1042vw solid dimgray; 
    padding: 0.5208vw; 
    transition: all 0.2s ease-in-out;
    box-sizing: border-box;
  }
  
  .HomeCarTxtClmn:hover {
    border-left: 0.4167vw double #ffffff; 
  }
  
  .TxtBrdr:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom right, black, white);
    translate: -150%;
    transition: all 0.3s ease-out;
    background-size: 400% 400%;
    animation: HrHomeCar 4s infinite ease-in-out running;
  }
  
  .TxtBrdr:hover:after {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    translate: 0;
    border-radius: 0.78125vw; 
  }
  
  .TxtBrdr {
    position: relative;
    margin: 0 0.5208vw; 
    text-align: justify;
    font-size: 1.3rem; 
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    padding: 0.2604vw; 
  }
  
  .HomeCarTxtClmnImg {
    width: 100%;
    opacity: 0.2;
    transition: all 0.3s;
    padding: 0.5208vw; 
    box-sizing: border-box;
  }
  
  .HomeCarTxtClmnImg:hover {
    opacity: 1;
    scale: 1.01;
    border-radius: 1.0417vw; 
    overflow: hidden;
  }
  
  .ArticleImg {
    width: 100%;
    max-width: 41.6667vw; 
    height: auto;
    aspect-ratio: 16 / 9; 
  }
  
  .ScrollChangeBg {
    position: relative;
    width: 100%;
  }
  
  .OrderNowBtn {
    all: unset;
    backdrop-filter: blur(0.5208vw); 
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translateX(-50%);
    scale: 1 1;
    transition: all 0.3s ease-in-out;
  }
  
  .OrderNowBtn:hover {
    scale: 1.08 1.04;
  }
  
  .OrderNowBtnLink {
    color: white;
    padding: 0.5208vw 1.5625vw; 
    text-decoration: none;
    border-radius: 0.78125vw; 
    transition: all 0.5s ease-out;
    font-size: 1rem; 
  }
  
  .OrderNowBtnLink:hover {
    background-image: linear-gradient(to right top, #374229, #204236, #133e43, #1f3849, #323144);
    animation: HrHomeCar 4s infinite ease-in-out running;
    background-size: 400% 400%;
  }
  
  @media screen and (max-width: 568px) {
    .HomeCarHr {
      margin: 2.0833vw auto 0;
      height: 0.25vw;
      border-radius: 0.625vw;
    }

    .CyberTruckImg {
        width: 100%;
        height: 30%;
    }
  
    .HomeCarBgCon {
        height: 30vh;
    }

    .HomeCarTxtRow {
      padding: 0.4167vw;
    }
  
    .HomeCarTxtClmn {
      padding: 0.4167vw;
      border-left: 0.0833vw solid dimgray;
    }
  
    .HomeCarTxtClmn:hover {
      border-left: 0.3333vw double #ffffff;
    }
  
    .TxtBrdr {
      font-size: 0.8rem; 
      margin: 0 0.4167vw;
      padding: 0.2083vw;
    }
  
    .HomeCarTxtClmnImg {
      padding: 0.4167vw;
    }
  
    .HomeCarTxtClmnImg:hover {
      border-radius: 0.8333vw;
    }
  
    .OrderNowBtn {
      backdrop-filter: blur(0.4167vw);
    }
  
    .OrderNowBtnLink {
      padding: 0.4167vw 1.25vw;
      font-size: 0.8rem; 
      border-radius: 0.625vw;
    }
  
    .ArticleImg {
      max-width: 33.3333vw;
    }
  }
  

  @media screen and (max-width: 768px) {
    .CyberTruckImg {
        width: 100%;
        height: 50%;
    }
    .HomeCarBgCon {
        height: 50vh;
    }
  }

  @media screen and (min-width: 768px) {
    .HomeCarHr {
      margin: 5.2083vw auto 0;
    }
  
    .CyberTruckImg {
        width: 100%;
        height: 50%;
    }

    .HomeCarBgCon {
        height: 50vh;
    }


    .HomeCarTxtRow {
      padding: 1.0417vw;
    }
  
    .HomeCarTxtClmn {
      width: 50%;
      padding: 0.5208vw 0 0.5208vw 1.0417vw;
    }
  
    .HomeCarTxtClmn:hover {
      border-left: 0.4167vw double #ffffff;
    }
  
    .TxtBrdr {
      font-size: 1rem; 
      width: 70%;
    }
  
    .HomeCarTxtClmnImg {
      width: 45%;
      padding: 0;
    }
  
    .OrderNowBtn {
      top: 70%;
      left: 45%;
      transform: none;
    }
  
    .OrderNowBtnLink {
      padding: 0.625vw 2.0833vw;
      font-size: 1rem; 
    }
  
    .ArticleImg {
      max-width: 41.6667vw;
    }
  }
  
  @media screen and (min-width: 1024px) {
    .HomeCarHr {
      margin: 7vw auto 0;
    }

    .CyberTruckImg {
        width: 100%;
        height: 70%;
    }
  
    .HomeCarTxtRow {
      height: 100vh;
      justify-content: space-evenly;
    }
  

    .HomeCarBgCon {
        height: 70vh;
    }

    .TxtBrdr {
      font-size: 1.2rem; 
    }
  
    .OrderNowBtnLink {
      padding: 0.78125vw 2.6042vw;
      font-size: 1.1rem; 
    }
  }
  
  @media screen and (min-width: 1200px) {
    .HomeCarHr {
      margin: 9.8958vw auto 0; 
    }
  
    .CyberTruckImg {
        width: 100%;
        height: 100%;
    }

    .HomeCarBgCon {
        height: 100vh;
    }

    .HomeCarTxtRow {
      height: 100vh;
      justify-content: space-evenly;
    }
  
    .TxtBrdr {
      font-size: 1.3rem; 
    }
  
    .OrderNowBtnLink {
      padding: 0.78125vw 2.6042vw;
      font-size: 1.2rem; 
    }
  
    .ArticleImg {
      max-width: 41.6667vw;
    }
  }