
.CarFooter {
    width: 100%;
    height: auto;
    background: #000;
    padding: 1rem 0;
  }
  
  .CarSectionFooter {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
  }
  
  .FooterLogo {
    width: 100%;
    height: auto;
    text-align: center;
  }
  
  .FLogo {
    width: 100%;
    max-width: 150px;
    height: auto;
  }
  
  .FooterInfos {
    width: 100%;
  }
  
  .FootersText {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    padding: 1rem;
  }
  
  .FooterSmedia {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 1rem;
  }
  
  .FootersTeam {
    width: 100%;
    font-size: 1.2rem;
    margin: 1rem 0;
    text-align: center;
  }
  
  .SocialMediaIcons {
    margin: 0.5rem;
  }
  
  .CopyRight {
    margin: 1rem auto;
    text-align: center;
  }
  
  .AddressInfos {
    font-size: 1rem;
    font-style: unset;
    text-align: center;
  }
  
  .LinksToPages {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    width: 100%;
    justify-content: center;
    gap: 1rem;
  }
  
  .Help_Items {
    margin: 1rem 0;
  }
  
  .Products {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Products_Link {
    text-decoration: none;
    margin: 0.75rem 0;
    color: aliceblue;
  }
  
  @media (min-width: 768px) {
    .CarFooter {
      padding: 2rem 0;
    }
  
    .FooterLogo {
      width: 20%;
    }
  
    .FLogo {
      max-width: 200px;
    }
  
    .FooterInfos {
      width: 80%;
    }
  
    .FootersText {
      padding: 0;
    }
  
    .FooterSmedia {
      justify-content: space-evenly;
    }
  
    .FootersTeam {
      width: 50%;
      font-size: 1.5rem;
      margin: 1.5rem auto;
    }
  
    .SocialMediaIcons {
      margin: 0.5rem 1.5rem;
    }
  
    .AddressInfos {
      font-size: 1.2rem;
    }
  
    .LinksToPages {
      justify-content: space-evenly;
    }
  }
  
  @media (min-width: 1024px) {
    .CarFooter {
      height: 500px;
    }
  
    .CarSectionFooter {
      height: 100%;
    }
  
    .FooterLogo {
      height: 100%;
    }
  
    .FootersText {
      height: 100%;
    }
  
    .LinksToPages {
      height: 100%;
    }
  }